:root {
    --PrimeryColor: hsl(225, 50%, 48%);
    --secoundaryColor: hsl(180, 17%, 95%);
    --secoundaryColor1: hsl(180, 84%, 26%);
    --colorOne: hsl(15, 74%, 70%);
    --colorTwo: hsl(28, 100%, 82%);
    --hoverColor: hsl(225, 56%, 54%);
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(0, 0%, 12%);
    --textColor: hsl(240, 1%, 48%);
    --borderColor: hsl(0, 0%, 83%);
    --WhiteColorDeam: hsl(0, 0%, 93%);
    --greyBg: hsl(0, 0%, 96%);
    --greyText: rgb(145, 145, 145);
    --inputColor: hsl(330, 12%, 97%);
    --boxShadow: box-shadow:rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
    --transition: .4s ease-in-out;
  
  }

  /* subscribe section style====================================> */
.subscribe {


  .sectionContainer {
    text-align: center;

    .inputDiv {
      margin-top: 1rem;
      flex-direction: column;
      background: var(--colorTwo);
      border-radius: 1rem;
      padding-bottom: 1rem;

      .input {
        border: none;
        outline: none;
        background: transparent;
        padding: 1rem .5rem;

        &:placeholder {
          font-size: 13px;
          opacity: .5;
        }
      }

      .btn {
        width: 40%;
        font-size: small;
      }
    }
  }
}
